export const TipoPantalla = () => {
    let disp = 'desktop'
    if (window.innerWidth < 768) {
        // console.log("Ests en un dispositivo mvil");
        disp = 'mobile'
    } else if (window.innerWidth < 992) {
        // console.log("Ests en una tablet");
        disp = 'tablet'
    } else {
        // console.log("Ests en un equipo desktop");
        disp = 'desktop'
    }
    return disp
}